import {
	ManageAccountsOutlined,
	EditOutlined,
	SchoolOutlined,
	WorkOutlineOutlined,
	FlagOutlined,
} from "@mui/icons-material";
import { Box, Typography, Divider, useTheme } from "@mui/material";
import UserImage from "../../components/UserImage";
import FlexBetween from "../../components/FlexBetween";
import WidgetWrapper from "../../components/WidgetWrapper";
import { useSelector } from "react-redux";
import { useEffect, useState } from "react";
import { useNavigate } from "react-router-dom";

const UserWidget = ({ userId, picturePath }) => {
	const [user, setUser] = useState(null);
	const { palette } = useTheme();
	const navigate = useNavigate();
	const token = useSelector((state) => state.token);
	const dark = palette.neutral.dark;
	const medium = palette.neutral.medium;
	const main = palette.neutral.main;

	const getUser = async () => {
		const response = await fetch(`/api/users/${userId}`, {
			method: "GET",
			headers: { Authorization: `Bearer ${token}` },
		});
		const data = await response.json();
		setUser(data);
	};

	useEffect(() => {
		getUser();
	}, []); // eslint-disable-line

	if (!user) {
		return null;
	}

	const { firstName, lastName, occupation, course, classmates, friends, nationality } = user;

	return (
		<WidgetWrapper>
			{/* FIRST ROW */}
			<FlexBetween
				gap='0.5rem'
				pb='1.1rem'
				onClick={() => navigate(`/profile/${userId}`)}
			>
				<FlexBetween gap='1rem'>
					<UserImage image={picturePath} />
					<Box>
						<Typography
							variant='h4'
							color={dark}
							fontWeight='500'
							sx={{
								"&:hover": {
									color: palette.primary.light,
									cursor: "pointer",
								},
							}}
						>
							{firstName} {lastName}
						</Typography>
						<Box>
							<Typography color={medium}>{friends.length} friends</Typography>
							<Typography color={medium}>
								{classmates.length} classmates
							</Typography>
						</Box>
					</Box>
				</FlexBetween>
				<ManageAccountsOutlined />
			</FlexBetween>
			<Divider />

			{/* SECOND ROW */}
			<Box p='1rem 0'>
				<Box display='flex' alignItems='center' gap='1rem' mb='0.5rem'>
					<SchoolOutlined fontSize='large' sx={{ color: main }} />
					<Typography color={medium}>{course}</Typography>
				</Box>
				<Box display='flex' alignItems='center' gap='1rem' mb='0.5rem'>
					<WorkOutlineOutlined fontSize='large' sx={{ color: main }} />
					<Typography color={medium}>{occupation}</Typography>
				</Box>
				<Box display='flex' alignItems='center' gap='1rem'>
					<FlagOutlined fontSize='large' sx={{ color: main }} />
					<Typography color={medium}>{nationality}</Typography>
				</Box>
			</Box>
            <Divider />

			{/* THIRD ROW */}
			<Box p='1rem 0'>
				<Typography fontSize='1rem' color={main} fontWeight='500' mb='1rem'>
					Social Profiles
				</Typography>

				{/* TWITTER */}
				<FlexBetween gap='1rem' mb='0.5rem'>
					<FlexBetween gap='1rem'>
						<img src='/assets/twitter.png' alt='twitter' />
						<Box>
							<Typography color={main} fontWeight='500'>
								Twitter
							</Typography>
							<Typography color={medium}>Social Network</Typography>
						</Box>
					</FlexBetween>
					<EditOutlined sx={{ color: main }} />
				</FlexBetween>

				{/* LINKEDIN */}
				<FlexBetween gap='1rem'>
					<FlexBetween gap='1rem'>
						<img src='/assets/linkedin.png' alt='linkedin' />
						<Box>
							<Typography color={main} fontWeight='500'>
								LinkedIn
							</Typography>
							<Typography color={medium}>Network Platform</Typography>
						</Box>
					</FlexBetween>
					<EditOutlined sx={{ color: main }} />
				</FlexBetween>
			</Box>
		</WidgetWrapper>
	);
};

export default UserWidget;
